import { GameData } from '../scripts/models/gameData';

export class GameStateGenerationService {
  // 
  private lastRawResponse: string = '';

  constructor() {
    console.log('');
  }

  /**
   * LLM
   * @returns LLM
   */
  public getLastRawResponse(): string {
    return this.lastRawResponse;
  }

  /**
   * 
   * @param choice 
   */
  public async generateNextGameState(choice: string): Promise<GameData | null> {
    // API
    if (typeof window.generate !== 'function') {
      console.warn('generate API');
      return null;
    }

    try {
      // 
      const systemPrompt = `<>${choice}</>`;

      // injectAIJSON
      const generateConfig = {
        user_input: choice,
        should_stream: false,
        injects: [
          {
            role: 'system',
            content: systemPrompt,
            position: 'before_prompt',
            depth: 0,
            should_scan: false,
          },
        ],
      };

      // API
      const result = await window.generate(generateConfig);

      // 
      this.lastRawResponse = result;
      console.log('%c[LLM]', 'color: #ff5722; font-weight: bold;');
      console.log(result);

      // 
      const regex = /<gametext>([\s\S]*?)<\/gametext>/;
      const match = result.match(regex);

      if (match && match[1]) {
        try {
          const jsonData = match[1].trim();
          console.log('%c[JSON]', 'color: #4caf50; font-weight: bold;');
          console.log(jsonData);
          const gameData = JSON.parse(jsonData);
          return gameData;
        } catch (parseError) {
          console.error('JSON:', parseError);
          const jsonData = match[1].trim();
          console.log('%c[JSON]', 'color: #f44336; font-weight: bold;', jsonData);
          return null;
        }
      } else {
        console.error('<gametext>');
        return null;
      }
    } catch (error) {
      console.error(':', error);
      return null;
    }
  }

  /**
   * 
   * @param memory 
   */
  public async generateMemoryState(memory: { id: number; content: string; image?: string }): Promise<GameData | null> {
    // API
    if (typeof window.generate !== 'function') {
      console.warn('generate API');
      return null;
    }

    try {
      // 
      const systemPrompt = `<>${memory.content}</>`;

      // injectAI
      const result = await window.generate({
        user_input: `"${memory.content}"`,
        should_stream: false,
        injects: [
          {
            role: 'system',
            content: systemPrompt,
            position: 'before_prompt',
            depth: 0,
            should_scan: false,
          },
        ],
      });

      // 
      this.lastRawResponse = result;
      console.log('%c[LLM]', 'color: #ff9800; font-weight: bold;');
      console.log(result);

      // 
      const regex = /<gametext>([\s\S]*?)<\/gametext>/;
      const match = result.match(regex);

      if (match && match[1]) {
        try {
          const jsonData = match[1].trim();
          console.log('%c[JSON]', 'color: #4caf50; font-weight: bold;');
          console.log(jsonData);
          const gameData = JSON.parse(jsonData);
          return gameData;
        } catch (parseError) {
          console.error('JSON:', parseError);
          const jsonData = match[1].trim();
          console.log('%c[JSON]', 'color: #f44336; font-weight: bold;', jsonData);
          return null;
        }
      }
    } catch (error) {
      console.error(':', error);
    }

    return null;
  }

  /**
   * 
   * @param action 
   */
  public async handleCustomAction(action: string): Promise<GameData | null> {
    // API
    if (typeof window.generate !== 'function') {
      console.warn('generate API');
      return null;
    }

    try {
      // 
      const systemPrompt = `<>${action}</>`;

      // injectAI
      const result = await window.generate({
        user_input: action,
        should_stream: false,
        injects: [
          {
            role: 'system',
            content: systemPrompt,
            position: 'before_prompt',
            depth: 0,
            should_scan: false,
          },
        ],
      });

      // 
      this.lastRawResponse = result;
      console.log('%c[LLM]', 'color: #2196f3; font-weight: bold;');
      console.log(result);

      // 
      const regex = /<gametext>([\s\S]*?)<\/gametext>/;
      const match = result.match(regex);

      if (match && match[1]) {
        try {
          const jsonData = match[1].trim();
          console.log('%c[JSON]', 'color: #4caf50; font-weight: bold;');
          console.log(jsonData);
          const gameData = JSON.parse(jsonData);
          return gameData;
        } catch (parseError) {
          console.error('JSON:', parseError);
          const jsonData = match[1].trim();
          console.log('%c[JSON]', 'color: #f44336; font-weight: bold;', jsonData);
          return null;
        }
      }
    } catch (error) {
      console.error(':', error);
    }

    return null;
  }

  /**
   * 
   * : window.debugLLMResponse()
   */
  public initDebugHelpers(): void {
    (window as any).debugLLMResponse = () => {
      console.log('%c[Debug] LLM', 'background: #222; color: #bada55');
      console.log(this.lastRawResponse);

      // gametext
      const regex = /<gametext>([\s\S]*?)<\/gametext>/;
      const match = this.lastRawResponse.match(regex);

      if (match && match[1]) {
        console.log('%c[Debug] gametext', 'background: #222; color: #ffa500');
        console.log(match[1]);

        try {
          const parsed = JSON.parse(match[1]);
          console.log('%c[Debug] JSON', 'background: #222; color: #4caf50');
          console.log(parsed);
        } catch (e) {
          console.log('%c[Debug] JSON', 'background: #222; color: #f44336');
          console.log(e);
        }
      } else {
        console.log('%c[Debug] gametext', 'background: #222; color: #f44336');
      }

      return '';
    };
  }
}
